import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { Router, Route, browserHistory, IndexRoute  } from 'react-router'
import VConsole from 'vconsole';
import createStore from './store/createStore'
import registerServiceWorker from './registerServiceWorker';
import sagas from './sagas'
import './components/AlertTip'
import './styles/init/rem'
import './styles/main.less'
import RouterWrapper from './components/Layouts/RouterWrapper'
import asyncComponent from './components/AsyncComponent'
import './utils/polyfills';
import { getUrlQuery } from './utils/url'

const { store, history } = createStore(window.__INITIAL_STATE__ || {}, browserHistory);
store.runSaga(sagas);

// 调试模式，路径后加上logger参数
if (getUrlQuery() && getUrlQuery().logger) {
  new VConsole();
}

const Tasks = asyncComponent(() => import('./pages/Tasks'))
const Page404 = asyncComponent(() => import('./pages/Page404'))
const TaskDetail = asyncComponent(() => import('./pages/TaskDetail'))
const IncomeRecord = asyncComponent(() => import('./pages/IncomeRecord'))
const CheckDetail = asyncComponent(() => import('./pages/CheckDetail'))
const UploadTaskOrder = asyncComponent(() => import('./pages/UploadTaskOrder'))
const NoviceTaskDetail = asyncComponent(() => import('./pages/NoviceTaskDetail'))
const InviteFriend = asyncComponent(() => import('./pages/InviteFriend'))
const NoviceTasks = asyncComponent(() => import('./pages/NoviceTasks'))
const NoviceStrategy = asyncComponent(() => import('./pages/NoviceStrategy'))
const Lottery = asyncComponent(() => import('./pages/Lottery'))

ReactDOM.render((
  <Provider store={store}>
    <Router history={history}>
      <Route path="/" component={RouterWrapper}>
        <IndexRoute component={Tasks} />
        <Route path="tasks" component={Tasks} />
        <Route path="taskDetail" components={TaskDetail} />
        <Route path="incomeRecord" component={IncomeRecord} />
        <Route path="checkDetail" component={CheckDetail} />
        <Route path="uploadTaskOrder" component={UploadTaskOrder} />
        <Route path="noviceTaskDetail" component={NoviceTaskDetail} />
        <Route path="inviteFriend" component={InviteFriend} />
        <Route path="noviceTasks" component={NoviceTasks} />
        <Route path="noviceStrategy" component={NoviceStrategy} />
        <Route path="lottery" component={Lottery} />
        <Route path="*" component={Page404} />
      </Route>
    </Router>
  </Provider>
), document.getElementById('root'));
registerServiceWorker();





