import { call, select } from 'redux-saga/effects';
import request from './request';

export function *apiCall (option) {
  if (Array.isArray(option)) {
    const [method, url, body] = option;
    option = {method, url, body};
  }
  // if (!__BROWSER__) {
  //   option.cookie = yield select(s => s.get('cookie'));
  // }
  option.cookie = document.cookie;
  // option.isWechat = yield select(s => s.getIn(['location', 'isWechat']));
  // option.isHybrid = yield select(s => s.getIn(['location', 'isHybrid']));
  // option.ua = yield select(s => s.getIn(['location', 'ua']));
  option.link = window.location.href;
  // option.hostHref = yield select(s => s.getIn(['location', 'hostHref']));
  // option.cookieT = yield select(s => s.get('cookie'));
  return yield call(request, option);
}



